/*=====================
  Базовая типография
=====================*/

@font-face {
	font-family: FugueMono;
	src: url("../font/fugue_mono-webfont.woff2") format("woff2");
	font-weight: 400;
	font-style: normal;
}

@font-face {
	font-family: FugueHeadline;
	src: url("../font/fugue_headline-webfont.woff2") format("woff2");
	font-weight: 400;
	font-style: normal;
}

@font-face {
	font-family: FugueRegular;
	src: url("../font/fugue_regular-webfont.woff2") format("woff2");
	font-weight: 400;
	font-style: normal;
}

body {
	font-family: FugueRegular, Arial, sans-serif;
	font-size: 16px;
	line-height: 20px;
	color: #000000;
}

.btn {
	display: inline-block;
	box-sizing: border-box;
	padding: 15px 10px;
	outline: none;
	border: 0;
	background-color: blue;
	color: #ffffff;
	cursor: pointer;
	cursor: pointer;
	text-align: center;
	vertical-align: top;
	text-decoration: none;
	font-family: FugueMono, Arial, sans-serif;
	font-size: 14px;
	line-height: 14px;
}

.btn:hover {
	opacity: 0.8;
}

.btn:active {
	opacity: 1;
}

.btn--big {
	margin: 0 auto 30px;
	padding: 20px 40px;
	font-size: 20px;
	line-height: 20px;
}

.btn:disabled {
	cursor: default;
	opacity: 0.5;
}

input[type="text"],
textarea,
select,
input[type="password"] {
	box-sizing: border-box;
	width: 100%;
	outline: none;
	font-family: FugueRegular, Arial, sans-serif;
	font-size: 18px;
	line-height: 20px;
}

label {
	cursor: pointer;
}

button {
	outline: none;
	cursor: pointer;
}

input::-webkit-input-placeholder {
	opacity: 0.8;
}

input::-moz-placeholde {
	opacity: 0.8;
}

/*=== компоненты ===*/

/*=== страницы ===*/

body {
	min-width: 1000px;
	background-color: blue;
}

.container {
	position: relative;
	box-sizing: border-box;
	min-width: 1000px;
	max-width: 1300px;
	min-height: 100vh;
	margin: 0 auto;
	border: 20px solid blue;
	background-color: #ffffff;
}

.header {
	padding: 40px 0;
	background-color: blue;
}

.header h1 {
	color: #ffffff;
	text-align: center;
	font-size: 50px;
	line-height: 60px;
}

.nav {
	margin-bottom: 30px;
	padding: 20px 0;
	background-color: blue;
	text-align: center;
	font-size: 0;
}

.nav a {
	display: inline-block;
	margin: 0 20px;
	padding-bottom: 5px;
	border-bottom: 2px solid blue;
	color: #ffffff;
	vertical-align: top;
	text-decoration: none;
	font-family: FugueMono, Arial, sans-serif;
	font-size: 16px;
	line-height: 16px;
}

.nav a:hover,
.nav a.active {
	border-bottom: 2px solid #ffffff;
}

.content {
	padding: 0 20px 0;
}

.tab-container {
	margin-bottom: 20px;
}

.tab-controls {
	display: flex;
	margin-bottom: 10px;
}

.tab-control {
	margin-right: 30px;
	padding: 15px 10px;
	outline: none;
	border: 4px solid blue;
	background: #ffffff;
	cursor: pointer;
	font-family: FugueMono, Arial, sans-serif;
	font-size: 14px;
	line-height: 14px;
}

.tab-control--active {
	background-color: blue;
	color: #ffffff;
}

.tab-content {
	display: none;
	line-height: 24px;
}

.tab-content--active {
	display: block;
}

hr {
	margin: 40px 0;
	border: 1px solid blue;
}

p {
	margin-top: 0;
}

.sticks {
	position: fixed;
	top: 30px;
	right: 0;
	z-index: 200;
	font-size: 0;
}

.sticks-item {
	display: block;
	width: 200px;
	margin-bottom: 10px;
	padding: 5px;
	border: 4px solid blue;
	border-right: 0;
	background-color: #ffffff;
	color: #000000;
	font-size: 16px;
	line-height: 16px;
}

.sticks-item span {
	color: blue;
	font-family: FugueMono, Arial, sans-serif;
}

.alert {
	position: relative;
	display: none;
	margin-bottom: 20px;
	padding: 10px;
	border: 4px solid #000000;
	color: #000000;
	font-size: 20px;
}

.alert .btn {
	position: absolute;
	top: 50%;
	right: 10px;
	padding: 5px;
	transform: translateY(-50%);
	font-size: 12px;
	line-height: 12px;
}

.alert--success {
	border: 4px solid green;
	color: green;
}

.alert--success .btn {
	background-color: green;
}

.alert--danger {
	border: 4px solid red;
	color: red;
}

.alert--danger .btn {
	background-color: red;
}

.alert--open {
	display: block;
}

.form-group {
	margin-bottom: 20px;
}

.form-group label {
	display: block;
	margin-bottom: 10px;
	font-size: 20px;
}

.form-group input[type="text"],
.form-group textarea {
	padding: 10px;
	border: 2px solid blue;
	font-family: FugueMono, Arial, sans-serif;
	font-size: 16px;
}

.form-group input[type="text"].error,
.form-group textarea.error {
	border-color: red;
}

input[type="checkbox"] {
	display: none;
}

input[type="checkbox"] + label {
	position: relative;
	display: block;
	padding-left: 40px;
}

input[type="checkbox"] + label::before {
	position: absolute;
	top: 50%;
	left: 0;
	width: 20px;
	height: 20px;
	border: 2px solid blue;
	content: "";
	transform: translateY(-50%);
}

input[type="checkbox"]:checked + label::after {
	position: absolute;
	top: 5px;
	left: 6px;
	width: 10px;
	height: 5px;
	border-bottom: 2px solid blue;
	border-left: 2px solid blue;
	content: "";
	transform: rotate(-45deg);
}

.map {
	height: 500px;
	margin-bottom: 20px;
	border: 2px solid blue;
	text-align: center;
	font-family: FugueHeadline, Arial, sans-serif;
	font-size: 30px;
	line-height: 500px;
}

.accordeon {
	margin-bottom: 20px;
	border: 2px solid blue;
}

.accordeon-item {
	padding: 10px;
	border-bottom: 2px solid blue;
}

.accordeon-item:last-child {
	border-bottom: 0;
}

.accordeon-head {
	color: blue;
	cursor: pointer;
	opacity: 0.7;
}

.accordeon-content {
	display: none;
}

.accordeon-item--active .accordeon-head {
	color: #000000;
	cursor: default;
	opacity: 1;
}

.accordeon-item--active .accordeon-content {
	display: block;
}

.editor-control .btn {
	width: 100%;
	display: block;
	margin-bottom: 10px;
	display: none;
}

.editor-control .btn.close {
	display: block;
}

.editor-control.active .btn {
	display: block;
}

