}

.modal-content {
	position: fixed;
	top: 50%;
	left: 50%;
	z-index: 10100;
	display: none;
	box-sizing: border-box;
	width: 600px;
	height: 400px;
	padding-top: 150px;
	background-color: #ffffff;
	transform: translate(-50%, -50%);
	text-align: center;
	font-family: FugueHeadline, Arial, sans-serif;
	font-size: 40px;
	line-height: 40px;
}

.modal-content.active {
	display: block;
}

